.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

::-webkit-scrollbar{
  width: 0;
}

a{
  text-decoration: none;
  color: inherit;
}
a:hover{
  color: inherit;
}

.btn{
  margin: 0 5px;
}

.content{
  margin-top: 100px;
}

.jumbotron, .jumbotron_detail{
  position: relative;
  background-image: linear-gradient(rgba(0,0,0,.5), rgba(0,0,0,.5)), url('./image1.jpg');
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.jumbotron .textGroup,
.textGroup_detail{
  height: 500px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: #fff;
  padding: 0 40px;
}
.textGroup_detail{
  height: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.main{
  margin-top: 80px;
}